.submit {
  border: 1px solid black;
  overflow: hidden;
  transition: all 0.3s ease-in-out;
}

.submit:hover {
  cursor: pointer;
}

.form-container {
  height: 50vh;
  position: absolute;
  margin-left: 500px;
  width: 400px;
  padding: 40px;
  background: rgba(0, 0, 0, 0.5);
  box-sizing: border-box;
  box-shadow: 0 15px 25px rgba(0, 0, 0, 0.6);
  border-radius: 10px;
}
textarea {
  width: 100%;
  height: 100px;
}
.form-container h2 {
  margin: 0 0 30px;
  padding: 0;
  color: white;
  text-align: center;
}

.form-container .input {
  position: relative;
}

.form-container .input input {
  width: 100%;
  padding: 10px 0;
  font-size: 16px;
  color: white;
  margin-bottom: 30px;
  border: none;
  border-bottom: 1px solid white;
  outline: none;
  background: transparent;
}
label {
  position: absolute;
  top: 0;
  left: 0;
  padding: 10px 0;
  font-size: 16px;
  color: white;
  pointer-events: none;
  transition: 0.5s;
}
.form-container .input input:focus ~ label,
.form-container .input input:valid ~ label {
  top: -20px;
  left: 0;
  color: #03e9f4;
  font-size: 12px;
}

.form-container form {
  position: relative;
  display: inline-block;
  padding: 10px 20px;
  color: #03e9f4;
  font-size: 16px;
  text-decoration: none;
  text-transform: uppercase;
  overflow: hidden;
  transition: 0.5s;
  margin-top: 40px;
  letter-spacing: 4px;
}

.form-container .submit:hover {
  background: #03e9f4;
  color: white;
  border-radius: 5px;
  box-shadow: 0 0 5px #03e9f4, 0 0 25px #03e9f4, 0 0 50px #03e9f4,
    0 0 100px #03e9f4;
}
