html,
body {
  height: 100%;
}

body {
  width: 100%;
  background: blueviolet;
  overflow-x: hidden;
  overflow-y: hidden;
}

.navbar {
  position: absolute;
  z-index: 4;
  top: 0;
}

.btn_group {
  margin-top: 20px;
  width: 200px;
  display: block;
  z-index: 3;
}

.container {
  right: 40px;
  left: 280px;
  top: 50px;
  position: absolute;
  padding-bottom: 0px;
}

.text-container {
  font-size: 18px;
  padding: 10px;
  line-height: 24px;
  color: white;
  display: inline-block;
  width: 65%;
  padding: 10px;
  margin: 3px 0px;
}

.panel {
  width: 100%;
  min-height: 100%;
  position: absolute;
  background-color: #000;
  box-shadow: 4px -4px 4px rgba(0, 0, 0, 0.2);
  margin-left: -102%;
  z-index: 2;
  -webkit-transition: all 0.6s ease-in-out;
  -moz-transition: all 0.6s ease-in-out;
  -o-transition: all 0.6s ease-in-out;
  -ms-transition: all 0.6s ease-in-out;
  transition: all 0.6s ease-in-out;
}

.panel:target {
  margin-left: 0%;
  background-color: blueviolet;
}

.btn {
  display: inline-block;
  padding: 20px 40px;
  margin: 10px 0;
  position: relative;
  color: white;
  border: 1px solid white;
  overflow: hidden;
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
  background: transparent;
  border-radius: 50px;
}

.btn:hover {
  color: violet;
}

.btn:hover:before {
  left: 0;
}
